// Start Header
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  background: rgba($color: #85bd90, $alpha: 0.76);
  &__menu {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include breakpoint(991) {
      min-height: 60px;
      position: unset;
    }
    &-logo {
      position: absolute;
      top: -20px;
      left: 0;
      z-index: 100;
      @include breakpoint(991) {
        left: 15px;
      }
      .logo {
        &__link {
          img {
            max-width: 230px;
            @include breakpoint(2559) {
              max-width: 160px;
            }
            @include breakpoint(1439) {
              max-width: 130px;
            }
            @include breakpoint(1199) {
              max-width: 120px;
            }
            @include breakpoint(991) {
              max-width: 96px;
            }
            // @include breakpoint(479) {
            //   max-width: 86px;
            // }
          }
        }
      }
    }
    &-button {
      $bar-width: 30px;
      $bar-height: 2px;
      $bar-spacing: 10px;
      width: $bar-width;
      height: $bar-height + $bar-spacing*2;
      cursor: pointer;
      z-index: 100;
      margin-left: auto;
      order: 2;
      @include min-breakpoint(992) {
        display: none;
      }
      .button {
        &__icon {
          position: relative;
          transform: translateY($bar-spacing);
          background: $black;
          transition: all 0 0.3s;
          &, &::after, &::before {
            width: $bar-width;
            height: $bar-height;
          }
          &::before, &::after {
            content: '';
            position: absolute;
            left: 0;
            background: $black;
          }
          &::before {
            bottom: $bar-spacing;
            transition: bottom 0.3s 0.3s cubic-bezier(0.23, 1, 0.32, 1), transform 0.3s cubic-bezier(0.23, 1, 0.32, 1);
          }
          &::after {
            top: $bar-spacing;
            transition: top 0.3s 0.3s cubic-bezier(0.23, 1, 0.32, 1), transform 0.3s cubic-bezier(0.23, 1, 0.32, 1);
          }
        }
      }
      &.animate {
        @include breakpointminmax(330, 374) {
          display: none;
        }
        .button {
          &__icon {
            &::before, &::after {
              background: $yellow;
            }
            background: transparent;
            &::before {
              bottom: 0;
              transform: rotate(-45deg);
              transition: bottom 0.3s cubic-bezier(0.23, 1, 0.32, 1), transform 0.3s 0.3s cubic-bezier(0.23, 1, 0.32, 1);
            }
            &::after {
              top: 0;
              transform: rotate(45deg);
              transition: top 0.3s cubic-bezier(0.23, 1, 0.32, 1), transform 0.3s 0.3s cubic-bezier(0.23, 1, 0.32, 1);
            }
          }
        }
      }
    }
    &-content {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-left: 350px;
      width: 100%;
      @include breakpoint(2559) {
        margin-left: 250px;
      }
      @include breakpoint(1679) {
        margin-left: 200px;
      }
      @include breakpoint(1439) {
        margin-left: 170px;
      }
      @include breakpoint(1199) {
        margin-left: 150px;
      }
      @include breakpoint(991) {
        display: block;
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        width: 100%;
        max-height: 0;
        z-index: 99;
        background: rgba($color: #85bd90, $alpha: 0.76);
        overflow-y: scroll;
        overflow-x: hidden;
        transition:  all 0.3s ease;
        margin-left: 0;
      }
      &::-webkit-scrollbar {
        width: 0;
      }
      &.show {
        max-height: 200vh;
        transition: all 2s ease;
      }
      .content {
        &__list {
          display: flex;
          align-items: center;
          @include breakpoint(991) {
            flex-direction: column;
            padding: 15px 0;
          }
          &-item {
            margin-right: 50px;
            @include breakpoint(1919) {
              margin-right: 30px;
            }
            @include breakpoint(991) {
              margin: 5px 0;
            }
            .item {
              &__link {
                font-family: $font-ravie;
                font-size: 30px;
                display: block;
                color: $black;
                padding: 50px 0;
                @include breakpoint(2559) {
                  padding: 30px 0;
                  font-size: 28px;
                }
                @include breakpoint(1919) {
                  font-size: 24px;
                }
                @include breakpoint(1679) {
                  font-size: 20px;
                }
                @include breakpoint(1439) {
                  padding: 25px 0;
                  font-size: 16px;
                }
                @include breakpoint(991) {
                  padding: 10px 0;
                }
                &:hover {
                  color: $yellow;
                }
              }
            }
          }
        }
        &__social {
          display: flex;
          align-items: center;
          @include breakpoint(991) {
            justify-content: center;
            margin: 10px 0 40px;
          }
          &-item {
            display: block;
            margin: 0 20px;
            @include breakpoint(1439) {
              margin: 0 10px;
            }
            img {
              width: 58px;
              @include breakpoint(2559) {
                width: 48px;
              }
              @include breakpoint(1919) {
                width: 38px;
              }
              @include breakpoint(1679) {
                width: 30px;
              }
            }
          }
        }
      }
    }
  }
    &.sticky {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.1);
    animation: .95s ease-in-out 0s normal none 1 running fadeInDown;
    transition: all 0.7s cubic-bezier(0.645, 0.045, 0.355, 1);
    z-index: 99;
  }
}
// End Header
