/* Start Section */
.section {
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  min-height: 100vh;
  padding: 50px 0;
  height: 100vh;
  @include breakpoint(991) {
    height: auto;
    min-height: auto;
  }
  &.features {
    background-image: url(../../images/layouts/features-background.jpg);
    background-position: bottom left;
    background-size: unset;
    @include breakpoint(2559) {
      background-position: center;
      background-size: cover;
    }
    @include breakpoint(991) {
      background-image: url(../../images/layouts/features-background-mobile.jpg);
      background-size: 100% 100%
    }
  }
  &.tokenomics {
    background-image: url(../../images/layouts/tokenomics-background.jpg);
    background-position: center top;
    background-size: unset;
    @include breakpoint(2559) {
      background-size: cover;
    }
    @include breakpoint(991) {
      background-image: url(../../images/layouts/tokenomics-background-mobile.jpg);
      background-size: 100% 100%
    }
  }
  &.roadmap {
    background-image: url(../../images/layouts/roadmap-background.jpg);
    background-position: center bottom;
    @include breakpoint(991) {
      background-image: url(../../images/layouts/roadmap-background-mobile.jpg);
      background-size: 100% 100%
    }
  }
  &__title {
    display: flex;
    align-items: center;
    background-image: url(../../images/layouts/section-item.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 600px;
    height: 400px;
    margin: auto;
    @include breakpoint(2559) {
      width: 450px;
      height: 200px;
    }
    @include breakpoint(1199) {
      width: 300px;
      height: 100px;
      margin-bottom: 50px;
    }
    &-content {
      font-family: $font-ravie;
      font-size: 36px;
      color: #ceb51b;
      -webkit-text-stroke: 2px #3C0540;
      margin-left: 200px;
      text-transform: capitalize;
      @include breakpoint(2559) {
        margin-left: 100px;
        -webkit-text-stroke: 3px #3C0540;
      }
      @include breakpoint(1919) {
        font-size: 36px;
        -webkit-text-stroke: 2px #3C0540;
      }
      @include breakpoint(1679) {
        font-size: 36px;
        -webkit-text-stroke: 2px #3C0540;
      }
      @include breakpoint(1199) {
        margin-left: 80px;
        font-size: 26px;
      }
      @include breakpoint(991) {
        -webkit-text-stroke: 1px #3C0540;
      }
      @include breakpoint(479) {
        margin-left: 60px;
        font-size: 24px;
      }
    }
  }
  &__box {
    display: flex;
    @include breakpoint(991) {
      flex-direction: column;
    }
    &-item {
      flex: 1;
      margin: 0 15px;
      background: rgba($color: #e5f8e1, $alpha: 0.5);
      border-radius: 10px;
      padding: 100px 50px;
  
      @include breakpoint(2559) {
        padding: 50px 30px;
      }
      @include breakpoint(1919) {
        padding: 25px 25px;
      }
      @include breakpoint(991) {
        margin: 0 0 30px;
      }
      &.white {
        background: rgba($color: #85bd90, $alpha: 0.5);
        .item {
          &__title, &__content, &__list li {
            color: white;
          }
        }
      }
      .item {
        &__title {
          text-align: center;
          font-family: $font-ravie;
          font-size: 30px;
          color: $black;
          text-transform: uppercase;
          margin-bottom: 50px;
          @include breakpoint(1919) {
            font-size: 26px;
            margin-bottom: 40px;
          }
          @include breakpoint(1679) {
            font-size: 24px;
            margin-bottom: 30px;
          }
          @include breakpoint(1439) {
            font-size: 20px;
          }
          @include breakpoint(1199) {
            font-size: 18px;
          }
          @include breakpoint(991) {
            font-size: 20px;
          }
          @include breakpoint(575) {
            font-size: 18px;
          }
        }
        &__content {
          color: $black;
          font-family: $font-agency;
          font-size: 30px;
          line-height: 2.5;
          @include breakpoint(2559) {
            line-height: 2;
          }
          @include breakpoint(1919) {
            font-size: 26px;
          }
          @include breakpoint(1679) {
            font-size: 24px;
          }
          @include breakpoint(1439) {
            font-size: 20px;
            line-height: 1.5;
          }
          @include breakpoint(1199) {
            font-size: 18px;
          }
        }
        &__list {
          list-style: square;
          max-height: 50vh;
          overflow: auto;
          &::-webkit-scrollbar {
            width: 0;
          }
          li {
            color: $black;
            font-family: $font-agency;
            font-size: 24px;
            margin-bottom: 30px;
            @include breakpoint(2559) {
              margin-bottom: 25px;
            }
            @include breakpoint(1919) {
              margin-bottom: 10px;
            }
            // @include breakpoint(1679) {
            //   margin-bottom: 15px;
            // }
            @include breakpoint(1439) {
              font-size: 20px;
              margin-bottom: 10px;
            }
            @include breakpoint(575) {
              font-size: 18px;
            }
          }
        }
      }
    }
  }
  &__image {
    padding: 50px;
    background: rgba($color: #e5f8e1, $alpha: 0.36);
    border-radius: 20px;
    text-align: center;
    width: 80vw;
    height: 65vh;
    margin: 0 auto;
    @include breakpoint(991) {
      height: auto;
      padding: 5px;
      width: 100%;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      @include breakpoint(991) {
        max-width: 100%;
        height: auto;
      }
    }
  }
}
/* End Section */