/* Start Footer */
.footer {
  position: relative;
  background-image: url(../../images/layouts/footer-background.jpg);
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  min-height: 150vh;
  @include breakpoint(2559) {
    min-height: 100vh;
  }
  @include breakpoint(991) {
    background-image: url(../../images/layouts/footer-background-mobile.jpg);
    background-size: 100% 100%;
  }
  &__content {
    padding: 100px 50px;
    @include breakpoint(575) {
      padding: 100px 0;
    }
    &-text {
      color: white;
      font-size: 24px;
      font-weight: 600;
      margin: 20px 0;
      @include breakpoint(767) {
        font-size: 22px;
      }
      @include breakpoint(575) {
        font-size: 20px;
      }
    }
    &-social {
      display: flex;
      align-items: center;
      .social {
        &__link {
          display: block;
          margin: 0 15px;
          img {
            width: 58px;
            @include breakpoint(2559) {
              width: 48px;
            }
            @include breakpoint(1919) {
              width: 38px;
            }
            // @include breakpoint(1679) {
            //   width: 30px;
            // }
          }
        }
      }
    }
  }
}
/* End Footer */