/* Start Banner */
.banner {
  position: relative;
  display: flex;
  align-items: center;
  background-image: url(../../images/layouts/banner-background.jpg);
  background-position: center right;
  background-repeat: no-repeat;
  background-size: unset;
  width: 100%;
  min-height: 100vh;
  padding: 100px 0 50px;
  @include breakpoint(2559) {
    background-position: center;
    background-size: cover;
  }
  @include breakpoint(991) {
    background-image: url(../../images/layouts/banner-background-mobile.jpg);
    background-size: 100% 100%;
  }
  &__content {
    text-align: center;
    margin-bottom: 50px;
    @include breakpoint(1919) {
      margin-bottom: 30px;
    }
    &-title {
      font-family: 'Maturasc';
      color: $yellow;
      font-size: 220px;
      margin-bottom: 50px;
      -webkit-text-stroke: 8px $purple;
      text-shadow: 0 49px 0 rgba($color: $purple, $alpha: 0.71);
      @include breakpoint(2559) {
        font-size: 150px;
        text-shadow: 0 30px 0 rgba($color: $purple, $alpha: 0.71);
        margin-bottom: 15px;
      }
      @include breakpoint(1919) {
        font-size: 120px;
        text-shadow: 0 25px 0 rgba($color: $purple, $alpha: 0.71);
        margin-bottom: 10px;
      }
      @include breakpoint(1679) {
        font-size: 100px;
        text-shadow: 0 20px 0 rgba($color: $purple, $alpha: 0.71);
        margin-bottom: 5px;
      }
      @include breakpoint(1439) {
        font-size: 90px;
      }
      @include breakpoint(767) {
        font-size: 80px;
        text-shadow: 0 15px 0 rgba($color: $purple, $alpha: 0.71);
      }
    }
    &-subtitle {
      font-family: $font-berlin;
      font-size: 60px;
      font-weight: 600;
      color: $black;
      @include breakpoint(2559) {
        font-size: 45px;
      }
      @include breakpoint(1919) {
        font-size: 35px;
      }
      @include breakpoint(1679) {
        font-size: 30px;
      }
      @include breakpoint(1439) {
        font-size: 26px;
      }
      @include breakpoint(767) {
        font-size: 24px;
      }
    }
  }
  &__box {
    display: flex;
    align-items: center;
    justify-content: center;
    @include breakpoint(575) {
      flex-direction: column;
    }
    &-item {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      margin-bottom: 30px;
      @include breakpoint(2559) {
        margin-bottom: 0;
      }
      @include breakpoint(575) {
        margin-bottom: 15px;
      }
      &:nth-child(2) {
        margin-left: 100px;
        @include breakpoint(1919) {
          margin-left: 50px;
        }
        @include breakpoint(1439) {
          margin-left: 30px;
        }
        @include breakpoint(575) {
          margin-left: 0;
        }
      }
      &.ellipse {
        background-image: url(../../images//layouts/ellipse.png);
        width: 560px;
        height: 155px;
        @include breakpoint(2559) {
          width: 400px;
          height: 110px;
        }
        @include breakpoint(1919) {
          width: 300px;
          height: 80px;
        }
        @include breakpoint(1679) {
          width: 250px;
          height: 69px;
        }
        @include breakpoint(1439) {
          width: 200px;
          height: 55px;
        }
      }
      &.polygon {
        background-image: url(../../images//layouts/polygon.png);
        width: 290px;
        height: 275px;
        @include breakpoint(2559) {
          width: 210px;
          height: 200px;
        }
        @include breakpoint(1919) {
          width: 160px;
          height: 150px;
        }
        @include breakpoint(1679) {
          width: 130px;
          height: 125px;
        }
        @include breakpoint(1439) {
          width: 100px;
          height: 95px;
        }
      }
      .item {
        &__content {
          color: $yellow;
          font-size: 60px;
          font-weight: 600;
          transition: all 0.3s ease;
          @include breakpoint(2559) {
            font-size: 40px;
          }
          @include breakpoint(1919) {
            font-size: 30px;
          }
          @include breakpoint(1679) {
            font-size: 24px;
          }
          @include breakpoint(1439) {
            font-size: 20px;
          }
        }
      }
      &:hover {
        .item__content {
          color: white;
        }
      }
    }
  }
  &__input {
    display: flex;
    flex-direction: column;
    margin: auto;
    max-width: 1125px;
    margin-top: 50px;
    @include breakpoint(2559) {
      max-width: 825px;
      margin-top: 20px;
    }
    @include breakpoint(1919) {
      max-width: 625px;
    }
    @include breakpoint(1439) {
      max-width: 425px;
    }
    @include breakpoint(575) {
      margin-top: 30px;
    }
    &-item {
      position: relative;
      input {
        width: 100%;
        background: rgba($color: $yellow, $alpha: 0.5);
        border-radius: 15px;
        font-size: 40px;
        font-weight: 600;
        padding: 30px 50px;
        @include breakpoint(2559) {
          font-size: 36px;
          padding: 20px 50px;
        }
        @include breakpoint(1679) {
          padding: 15px 40px;
          font-size: 32px;
        }
        @include breakpoint(1439) {
          padding: 10px 30px;
          font-size: 28px;
        }
        @include breakpoint(1199) {
          font-size: 24px;
        }
        @include breakpoint(991) {
          font-size: 20px;
        }
        @include breakpoint(767) {
          font-size: 18px;
        }
        // @include breakpoint(575) {
        //   font-size: 16px;
        // }
      }
      .item {
        &__button {
          position: absolute;
          top: 0;
          right: 0;
          @include breakpoint(2559) {
            max-width: 80px;
          }
          @include breakpoint(1679) {
            max-width: 65px;
          }
          @include breakpoint(1439) {
            max-width: 50px;
          }
          @include breakpoint(1199) {
            max-width: 45px;
          }
          @include breakpoint(991) {
            max-width: 40px;
          }
          // @include breakpoint(575) {
          //   max-width: 35px;
          // }
        }
      }
    }
    label {
      font-size: 24px;
      font-weight: 600;
      color: $black;
      margin-bottom: 10px;
      @include breakpoint(1919) {
        font-size: 20px;
      }
      @include breakpoint(1439) {
        font-size: 18px;
      }
      @include breakpoint(991) {
        font-size: 16px;
      }
    }
    &-button {
      margin-top: 20px;
      button {
        display: inline-block;
        color: $black;
        font-size: 24px;
        font-weight: 600;
        text-decoration: underline;
        text-align: left;
        transition: all 0.3s ease;
        &:hover {
          color: $yellow;
        }
        @include breakpoint(1439) {
          font-size: 20px;
        }
        @include breakpoint(991) {
          font-size: 16px;
        }
      }
    }
  }
}
/* End Banner */