* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
ul,
li {
  list-style: none;
  margin: 0;
}
p {
  margin: 0;
}
a {
  color: inherit;
  text-decoration: none;
  transition: all 0.3s ease;
  &:hover {
    color: inherit;
    text-decoration: none;
  }
}
img {
  max-width: 100%;
}
body {
  font-family: $font-berlin;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.3;
  color: #fff;
}
.wrapper {
  overflow: hidden;
}

input,
textarea,
button {
  border: 0;
  box-shadow: none;
  outline: none;
  &:focus {
    outline: none;
  }
}

button {
  background:  transparent;
  cursor: pointer;
}

textarea {
  resize: none;
}

.container {
  &.custom {
    @include min-breakpoint(1440) {
      max-width: 85vw;
    }
    @include min-breakpoint(1680) {
      max-width: 85vw;
    }
    @include min-breakpoint(1920) {
      max-width: 85vw;
    }
    @include min-breakpoint(2680) {
      max-width: 85vw;
    }
  }
  &-fluid {
    &.custom {
      @include min-breakpoint(1440) {
        padding: 0 30px;
      }
    }
  }
}


.btn {
  display: inline-block;
  position: relative;
  padding: 5px 15px;
  font-size: 16px;
  line-height: 1.5;
  cursor: pointer;
  border-radius: 6px;
  text-transform: capitalize;
}